<template>
    <div class="school-detail-wrapper">
        <div class="banner"></div>
        <div class="school-detail-main">
            <div class="breadcrumd-wrapper">
                <span class="l-title"></span>
                <v-breadcrumb separator=">">
                    <v-breadcrumb-item>
                        <router-link to="/"></router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>
                        <router-link to="/school-search"></router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>Eton College</v-breadcrumb-item>
                </v-breadcrumb>
            </div>
            <div class="school-panel">
                <div class="school-logo"></div>
                <div class="school-info">
                    <h3 class="school-name"> Eton College</h3>
                    <p class="school-desc">342</p>
                    <div class="btn-wrapper">
                        <button class="contrast-btn"></button>
                        <button class="compare-btn"></button>
                    </div>
                </div>
            </div>
            <div class="school-shows">
                <div class="show-left">
                    <v-carousel
                    v-model="value2"
                    :arrow="'always'"
                    :dots="'none'">
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                    </v-carousel>
                    <ul class="school-characteristics">
                        <li v-for="s in scList" :key="s.id">
                            <span class="title">{{s.title}}</span>
                            <span class="result">{{s.result}}</span>
                        </li>
                    </ul>
                </div>
                <div class="show-right"></div>
            </div>
            <div class="school-details-panel">
                <ul class="detail-tabs">
                    <li v-for="(t, index) in tabList" :key="index"
                    :class="{'active':tabSelected == index}"
                    @click="tabSelected = index">{{t.name}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            value2: 0,
            scList: [
                {
                    title: '',
                    result: ''
                },
                {
                    title: '',
                    result: ''
                },
                {
                    title: '',
                    result: '13-18'
                },
                {
                    title: '',
                    result: '8%'
                },
                {
                    title: '',
                    result: '500000'
                },
                {
                    title: '',
                    result: '10'
                },
                {
                    title: '',
                    result: ''
                },
                {
                    title: '',
                    result: '1440'
                },
                {
                    title: '',
                    result: '100'
                },
                {
                    title: '',
                    result: '1:0.5'
                },
                {
                    title: '',
                    result: '50000'
                },
                {
                    title: '',
                    result: ''
                }
            ],
            tabList: [
                {
                    name: ''
                },
                {
                    name: ''
                },
                {
                    name: ''
                },
                {
                    name: ''
                }
            ],
            tabSelected: 0
        }
    }
}
</script>

<style>

</style>



